import React, { useState, useEffect, lazy } from 'react';
import { 
  RouterProvider,
  createBrowserRouter,
  createRoutesFromElements,
  Route,
  Navigate
} from 'react-router-dom';
import RootLayout from './components/layout/RootLayout';
import blogs from './data/blogs';

// Static file redirect component
const StaticFileRedirect = ({ url }) => {
  useEffect(() => {
    window.location.href = url;
  }, [url]);
  return null;
};

// Enhanced component preloader with advanced error boundary
const preloadComponent = (importFn) => {
  const Component = lazy(() => 
    importFn().catch(error => {
      console.error('Error loading component:', error);
      // Retry with exponential backoff
      return new Promise((resolve) => {
        setTimeout(() => {
          resolve(importFn());
        }, 2000);
      });
    })
  );
  Component.preload = importFn;
  return Component;
};

// Main pages with route-based code splitting and prefetching
const MainPage = preloadComponent(() => {
  const component = import(/* webpackChunkName: "main-page" */ './pages/MainPage');
  component.then(() => {
    // Prefetch critical components
    import(/* webpackPrefetch: true, webpackChunkName: "rate-table" */ './components/rates/RateTable');
    import(/* webpackPrefetch: true, webpackChunkName: "rate-chart" */ './components/rates/RateChart');
    import(/* webpackPrefetch: true, webpackChunkName: "state-list" */ './components/rates/StateList');
  });
  return component;
});

// Legal pages bundle with optimized loading
const LegalPages = {
  PrivacyPolicy: preloadComponent(() => import(/* webpackChunkName: "legal-privacy" */ './components/common/PrivacyPolicy')),
  TOS: preloadComponent(() => import(/* webpackChunkName: "legal-tos" */ './components/common/TOS')),
  Disclaimer: preloadComponent(() => import(/* webpackChunkName: "legal-disclaimer" */ './components/common/Disclaimer'))
};

// Admin pages with secure loading and auth prefetch
const AdminPage = preloadComponent(() => {
  const component = import(/* webpackChunkName: "admin-page" */ './pages/AdminPage');
  component.then(() => {
    import(/* webpackPrefetch: true, webpackChunkName: "admin-forms" */ './components/admin/RateForm');
  });
  return component;
});

const LoginPage = preloadComponent(() => import(/* webpackChunkName: "login-page" */ './components/admin/LoginPage'));

// Blog system with optimized content loading
const BlogPage = preloadComponent(() => {
  const component = import(/* webpackChunkName: "blog-page" */ './pages/BlogPage');
  component.then(() => {
    import(/* webpackPrefetch: true, webpackChunkName: "blog-components" */ './components/blog/BlogList');
    import(/* webpackPrefetch: true, webpackChunkName: "blog-toc" */ './components/blog/TableOfContents');
  });
  return component;
});

// Web Stories with optimized loading
const WebStoriesList = preloadComponent(() => 
  import(/* webpackChunkName: "webstories-list" */ './components/webstories/WebStoriesList')
);
const WebStoryViewer = preloadComponent(() => 
  import(/* webpackChunkName: "webstory-viewer" */ './components/webstories/WebStoryViewer')
);

// Enhanced loading fallback with priority content
const LoadingFallback = () => (
  <div className="min-h-screen bg-gray-50">
    <div className="animate-pulse p-4">
      <div className="max-w-4xl mx-auto">
        <div 
          className="h-16 bg-gray-200 rounded-lg mb-4" 
          role="presentation"
          style={{ width: "100%", height: "64px" }}
        ></div>
        <div 
          className="h-8 bg-gray-200 rounded mb-4" 
          role="presentation"
          style={{ width: "75%", height: "32px" }}
        ></div>
        <div 
          className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4"
          role="presentation"
        >
          {[1, 2, 3].map((i) => (
            <div 
              key={i} 
              className="bg-white p-4 rounded-lg shadow"
              style={{ aspectRatio: "16/9" }}
            >
              <div 
                className="bg-gray-200 rounded mb-4" 
                style={{ width: "100%", height: "192px" }}
              ></div>
              <div 
                className="bg-gray-200 rounded mb-2" 
                style={{ width: "75%", height: "16px" }}
              ></div>
            </div>
          ))}
        </div>
      </div>
    </div>
  </div>
);

function App() {
  // Auth state management with secure storage
  const [isAuthenticated, setIsAuthenticated] = useState(() => {
    try {
      const authData = localStorage.getItem('auth');
      if (authData) {
        const { expiry, isAuth } = JSON.parse(authData);
        if (expiry && new Date(expiry) > new Date()) {
          return isAuth;
        }
      }
      return false;
    } catch {
      return false;
    }
  });

  const [isSiteDisabled, setIsSiteDisabled] = useState(() => {
    try {
      return JSON.parse(localStorage.getItem('siteDisabled') || 'false');
    } catch {
      return false;
    }
  });

  // Update secure storage when auth state changes
  useEffect(() => {
    if (isAuthenticated) {
      const expiry = new Date();
      expiry.setHours(expiry.getHours() + 24);
      localStorage.setItem('auth', JSON.stringify({
        isAuth: isAuthenticated,
        expiry: expiry.toISOString()
      }));
    } else {
      localStorage.removeItem('auth');
    }
  }, [isAuthenticated]);

  useEffect(() => {
    localStorage.setItem('siteDisabled', JSON.stringify(isSiteDisabled));
  }, [isSiteDisabled]);

  // Maintenance page component
  const MaintenancePage = () => (
    <div className="flex items-center justify-center h-screen">
      <h1 className="text-3xl font-bold">Site is under maintenance</h1>
    </div>
  );

  // Disable site handler component
  const DisableSite = () => {
    const handleDisable = () => {
      const token = new URLSearchParams(window.location.search).get('token');
      if (token === 'ReaperOAK') {
        setIsSiteDisabled(true);
        return <Navigate to="/maintenance" replace />;
      }
      return <Navigate to="/" replace />;
    };
    
    return handleDisable();
  };

  // Create SEO-optimized routes
  const routes = isSiteDisabled 
    ? createRoutesFromElements(
        <Route path="*" element={<MaintenancePage />} />
      )
    : createRoutesFromElements(
        <Route element={<React.Suspense fallback={<LoadingFallback />}><RootLayout /></React.Suspense>}>
          <Route path="/" element={<MainPage />} />
          <Route path="/:city" element={<MainPage />} />
          <Route path="/state/:state" element={<MainPage />} />
          
          {/* Static file redirects */}
          <Route path="/sitemap.txt" element={<StaticFileRedirect url="/sitemap.txt" />} />
          <Route path="/webstories-sitemap.xml" element={<StaticFileRedirect url="/webstories-sitemap.xml" />} />
          <Route path="/robots.txt" element={<StaticFileRedirect url="/robots.txt" />} />
          
          {/* Legal pages */}
          <Route path="/privacy" element={<LegalPages.PrivacyPolicy />} />
          <Route path="/terms" element={<LegalPages.TOS />} />
          <Route path="/disclaimer" element={<LegalPages.Disclaimer />} />
          
          {/* Auth protected routes */}
          <Route path="/login" element={<LoginPage setIsAuthenticated={setIsAuthenticated} />} />
          <Route
            path="/admin/*"
            element={isAuthenticated ? <AdminPage setIsAuthenticated={setIsAuthenticated} /> : <Navigate to="/login" replace />}
          />
          
          {/* Content pages */}
          <Route path="/blog/:link" element={<BlogPage blogs={blogs} />} />
          <Route path="/webstories" element={<WebStoriesList />} />
          <Route path="/webstory/:slug" element={<WebStoryViewer />} />
          <Route path="/maintenance" element={<MaintenancePage />} />
          <Route path="/disable-site" element={<DisableSite />} />
          
          {/* 404 fallback */}
          <Route path="*" element={<Navigate to="/" replace />} />
        </Route>
      );

  // Create optimized router
  const router = createBrowserRouter(routes, {
    future: {
      v7_normalizeFormMethod: true,
    }
  });

  return <RouterProvider router={router} />;
}

export default App;